import { responsiveProperty } from '@mui/material/styles/cssUtils';
import MultiGrid from './MultiGrid';
import React, { useEffect, useState } from 'react';
import MultiGameTile from '../MulitGameTile';
const HskMultiGame = () => {
    function getGame() {
        var whichGame = window.location.href.substring(window.location.href.length - 4, window.location.href.length);
        return whichGame;
    }

    return (
        <div>
            <h1>{getGame()}</h1>
            <MultiGrid
            Tile1={<MultiGameTile TextValue="Tile 1" />}
            Tile2={<MultiGameTile TextValue="Tile 2" />}
            Tile3={<MultiGameTile TextValue="Tile 3" />}
            Tile4={<MultiGameTile TextValue="Tile 4" />}

             />
        </div>
    )
}
export default HskMultiGame;